@import url(https://cdn.jsdelivr.net/npm/animate.css@3.5.2/animate.min.css);
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.container {
  max-width: 800px;
  margin: 0 auto;
  display: grid;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.container img {
    justify-self: center;
}

* {
  font-family: Arial, Helvetica, sans-serif;
}
.container {
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 15px;
}
.options {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 10px;
  border-bottom: 1px solid #e0e0e0;
}
.options.bottom {
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: end;
}
.options.bottom button {
      margin: 0px 10px;
      border: 0px;
      padding: 10px;
      border-radius: 5px;
      background: #35495e;
      color: whitesmoke;
}
.options.bottom button.active {
        background: #42b883;
        border: 0px;
}
.options span {
    -ms-flex-item-align: center;
        align-self: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.todo-input {
  padding: 15px;
  margin: 15px;
  font-size: 20px;
}
.todo-input:focus {
    outline: 0;
}
.edit-todo {
  grid-row: 2/3;
  grid-column: 1/-1;
  margin: 15px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
  border: 0px;
}

.todo {
  margin: 15px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
  display: grid;
  grid-auto-columns: -webkit-min-content;
  grid-auto-columns: min-content;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  grid-template-columns: repeat(3, 1fr);
}
.todo .remove-todo {
    grid-column: 3/4;
    justify-self: end;
}
.todo .done-todo {
    justify-self: start;
}
.todo .doneTask {
    text-decoration: line-through;
    opacity: 0.5;
}
